@import '../../assets/sass/variables.scss';
.dashboard {
	display: flex;
	&__container {
		flex: 6;
		.widgets,
		.charts {
			display: flex;
			padding: 20px;
			gap: 20px;
		}
		.charts {
			padding: 5px 20px;
		}
		.list-container {
			-webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
			box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
			padding: 20px;
			margin: 20px;

			&__item {
				font-weight: $fontWeightMedium;
				color: $grayColor;
				margin-bottom: 15px;
			}
		}
	}
}
